@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav.sticky {
    z-index: 1;
    background-color: ;
    position: fixed;
    margin-top: 8vh;
    top: 0;
    padding: 45px;
    font-size: 20px;
    float: left;
}

.ul-nav {
    padding-left: 75px;
}

.li-nav {
    /* display: inline; */
    list-style-type: none;
    padding-bottom: 75px;
    font-size: larger;
}

a {
    text-decoration: none;
}

@media (min-width:1025px) {
    .navbar-brand{
        display: none;
    }
}

.navbar-start{
    flex-grow: 1;
    justify-content: center;
    text-align: center;
}

@media screen and (max-width: 1024px){
    #logo{
        display: none;
    }
    .force-center{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        font-size: x-large;
    }
    .navbar-end{
        display: flex;
        justify-content: center;
    }
}

.header-container {
    padding-top: 30vh;
    background-image: url(/react-portfolio/static/media/pink-cloud-mountain.cdb50ada.jpg);
    background-color: black;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    height: 100vmax;
    width: 100vw;
}
.work-container {
    background-color: unset;
    margin-top: 5%;
}

.work-title {
    margin-bottom: 10%;
}

.list-group {

}

.list-group-item {
    list-style-type: none;
    margin-bottom: 10%;
    padding-right: 40px; /*  This is to offeset whatever is giving this a padding-inline-start value of 40px  */
}

.project-image {
    width: 500px;
}

.description-text {
    font-size: larger;
    padding-bottom: 15px;
}

.link-button {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    /* width: 100%; */
    background:#56aee1;
    border-radius:5px;
    border:0;
    cursor:pointer;
    color:white;
    font-size:20px;
    padding-top:10px;
    padding-bottom:10px;
    transition: all 0.3s;
    margin-top:-4px;
    font-weight:700;
}
.linkl-button:hover {
    background:#4977cc; 
}

.deployed-link {
    margin-right: 10%;
}

.repo-link {

}
.contact-container {
  
}

form {
  max-width:420px;
  margin:50px auto;
  /* margin-top: 20%; */
  margin-bottom: 20%;
}

.feedback-input {
  /* color:white; */
  font-family: Helvetica, Arial, sans-serif;
  font-weight:500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border:2px solid #56aee1;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width:100%;
  box-sizing: border-box;
  outline:0;
}

.feedback-input:focus { border:2px solid #4977cc; }

textarea {
  height: 150px;
  line-height: 150%;
  resize:vertical;
}

[type="submit"] {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  width: 100%;
  background:#56aee1;
  border-radius:5px;
  border:0;
  cursor:pointer;
  color:white;
  font-size:24px;
  padding-top:10px;
  padding-bottom:10px;
  transition: all 0.3s;
  margin-top:-4px;
  font-weight:700;
}
[type="submit"]:hover { background:#4977cc; }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

